.app{
    position: relative;
}

.clickMeDiv{
    position: absolute;
    vertical-align: middle;
    top: 80%;
    left: 0;
    right: 0;
    margin: auto;
    width: 10%;
    height: 8%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.clickMeDiv >span{
    display: inline-block;
    font-size: 16px;
    text-align: center;
    color: white;
    margin: 0 auto;
}

.heart{
    border: none;
    outline: none;
    background-position: center;
    transition: 0.1s;
    width: 8vw;
    height: 30px;
    margin: 0 auto;
}

.heart >img{
    width: 100%;
    height: auto;
    margin: 0 auto;
}

.heart:active {
    width: 13vw;
}

.show{
    width: 100%;
    height: 30vh;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: absolute;
    top: 20vh;
}

.show >p{
    display: inline-block;
    font-size: 17px;
    width: 90%;
    text-align: center;
    color: white;
    margin-bottom: 60px;
}
.show >img{
    width: 80vw;
    height: 350px;
    object-fit: contain;
    margin: 0 auto;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.fadeIn {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-fill-mode: both;
}

.fadeInMore {
    animation-name: fadeIn;
    animation-duration: 2s;
    animation-delay: 4s;
    animation-fill-mode: both;
}

